import React from 'react'

class StockTableHeader extends React.Component {

    render() {
        return (
            <thead>
            <tr>
                <th>Etat</th>
                <th>Modle</th>
                <th>Marque</th>
                <th className="mobile-hide">Reference</th>
                <th>En Stock</th>
                <th>Actions</th>
            </tr>
            </thead>
        )
    }
}

export default StockTableHeader


